import React from 'react';
import PropTypes from 'prop-types';
import styles from './contacts.module.css';

const Contacts = ({ contacts }) => {
  return (
    <address>
      <ul className={styles.contacts}>
        <li className={styles.contactsItem}>
          <svg className={styles.icon}>
            <use href={'/images/SVG/Sprite.svg#phone'}></use>
          </svg>
          <a
            href={`tel:${contacts.phone}`}
            className={styles.phone}
            target="_blank"
            rel="noreferrer"
          >
            {contacts.phone}
          </a>
        </li>
        <li className={styles.contactsItem}>
          <svg className={styles.icon}>
            <use href={'/images/SVG/Sprite.svg#email'}></use>
          </svg>
          <a href={`mailto:${contacts.email}`} target="_blank" rel="noreferrer">
            {contacts.email}
          </a>
        </li>
        <li className={styles.contactsItem}>
          <svg className={styles.icon}>
            <use href={'/images/SVG/Sprite.svg#github'}></use>
          </svg>
          <a href={contacts.github} target="_blank" rel="noreferrer">
            www.github.com
          </a>
        </li>
        <li className={styles.contactsItem}>
          <svg className={styles.icon}>
            <use href={'/images/SVG/Sprite.svg#linkedin'}></use>
          </svg>
          <a href={contacts.linkedin} target="_blank" rel="noreferrer">
            www.linkedin.com
          </a>
        </li>
        <li className={styles.contactsItem}>
          <svg className={styles.icon}>
            <use href={'/images/SVG/Sprite.svg#location'}></use>
          </svg>
          <a href={contacts.locationMap} target="_blank" rel="noreferrer">
            {contacts.location}
          </a>
        </li>
      </ul>
    </address>
  );
};

Contacts.propTypes = {
  contacts: PropTypes.shape({
    phone: PropTypes.string.isRequired,
    email: PropTypes.string.isRequired,
    github: PropTypes.string,
    linkedin: PropTypes.string,
    location: PropTypes.string,
    locationMap: PropTypes.string,
  }),
};

export default Contacts;
